import React from 'react';

const blogPosts = [
  {
    title: "Top 5 Chest Workouts to Build Muscle Fast",
    image: "https://images.unsplash.com/photo-1605296867304-46d5465a13f1?auto=format&fit=crop&w=800&q=60",
    summary: "Learn the most effective exercises to grow your chest and improve upper body strength.",
    link: "https://www.muscleandstrength.com/workouts/build-massive-chest.html"
  },
  {
    title: "Nutrition Tips for Lean Muscle Gain",
    image: "data:image/jpeg;base64,",
    summary: "Discover essential nutrition strategies to support your fitness and muscle-building goals.",
    link: "https://www.bodybuilding.com/content/10-best-nutrition-tips.html"
  },
  {
    title: "Morning vs Evening Workouts  Whats Better?",
    image: "https://images.unsplash.com/photo-1558611848-73f7eb4001a1?auto=format&fit=crop&w=800&q=60",
    summary: "Explore the benefits of morning and evening workouts to find what fits your schedule best.",
    link: "https://www.verywellfit.com/morning-vs-evening-workout-4159453"
  },
  {
    title: "How to Stay Motivated for Gym Every Day",
    image: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSozvIQyEx6N-sbtrChdHKh1LQZAbVyQo09_Q&s",
    summary: "Simple techniques to build consistent habits and overcome workout fatigue or laziness.",
    link: "https://medium.com/motivation-grid/stay-motivated-to-workout-ef6bcfcdc32"
  }
];

const Blogs = () => {
  return (
    <div style={{ padding: '20px' }}>
      <h1 style={{ textAlign: 'center' }}>Fitness Blogs</h1>
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))', gap: '20px', marginTop: '30px' }}>
        {blogPosts.map((blog, index) => (
          <a
            key={index}
            href={blog.link}
            target="_blank"
            rel="noopener noreferrer"
            style={{ textDecoration: 'none', color: 'inherit' }}
          >
            <div style={{ border: '1px solid #ccc', borderRadius: '10px', overflow: 'hidden', boxShadow: '0 2px 8px rgba(0,0,0,0.1)' }}>
              <img src={blog.image} alt={blog.title} style={{ width: '100%', height: '180px', objectFit: 'cover' }} />
              <div style={{ padding: '10px' }}>
                <h3>{blog.title}</h3>
                <p>{blog.summary}</p>
              </div>
            </div>
          </a>
        ))}
      </div>
    </div>
  );
};

export default Blogs;
